@import url("https://fonts.googleapis.com/css2?family=Comfortaa");

* {
	margin: 0;
	padding: 0;
	outline: none;
	box-sizing: border-box;
	font-family: "Comfortaa", cursive;
	font-size: 14px;
}

body {
	width: 100%;
	height: 100vh;
}

#root {
	height: 100%;
}

.container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
}
